import { Directive, Renderer2, OnInit, ElementRef, HostListener, HostBinding, Input } from '@angular/core';

@Directive({
  selector: '[appBetterHighlight]'
})
export class BetterHighlightDirective implements OnInit {
  @Input() defaultColor:{bgcolor, color} = {
    bgcolor: 'yellow',
    color: 'black'
  }
  @Input('appBetterHighlight') highlightColor:{bgcolor, color} = {
    bgcolor: 'blue',
    color: 'white'
  }


  @HostBinding('style.backgroundColor') backgroundColor: string;
  @HostBinding('style.color') color: string;

  constructor(private elRef: ElementRef ,private renderer:Renderer2) { }

  ngOnInit() {
    // this.renderer.setStyle(this.elRef.nativeElement, 'background-color', 'yellow');
    this.backgroundColor = this.defaultColor.bgcolor;
    this.color = this.defaultColor.color;

    setInterval(() => {
      if (this.backgroundColor == this.defaultColor.bgcolor) {
        this.backgroundColor = this.highlightColor.bgcolor;
        this.color = this.highlightColor.color;
      } else {
        this.backgroundColor = this.defaultColor.bgcolor;
        this.color = this.defaultColor.color;
      }
    }, 1500);
  }

  @HostListener('mouseenter') onMouseEnter(e: Event) {
    // this.renderer.setStyle(this.elRef.nativeElement, 'background-color', 'blue');
    // this.renderer.setStyle(this.elRef.nativeElement, 'color', 'white');
    this.backgroundColor = this.highlightColor.bgcolor;
    this.color = this.highlightColor.color;
  }

  @HostListener('mouseleave') onMouseLeave(e: Event) {
    // this.renderer.setStyle(this.elRef.nativeElement, 'background-color', 'yellow');
    // this.renderer.setStyle(this.elRef.nativeElement, 'color', 'black');
    this.backgroundColor = this.defaultColor.bgcolor;
    this.color = this.defaultColor.color;   
  }
}



