export function Timer(time, interval, cb, cbEnd?) {
    let _time = 0;
    const _interval = setInterval(() => {
        let val = cb(time - _time);
        
        if (val) 
            clearInterval(_interval);
        
        _time += interval;
        // console.log(_time, interval);
        if (_time > time) {
            cbEnd();
            clearInterval(_interval);
        }
    }, interval)
}



