import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-single-server',
  templateUrl: './single-server.component.html',
  styleUrls: ['./single-server.component.css']
})
export class SingleServerComponent implements OnInit {
  @Input() serverId = 10;
  @Input() connected = false;

  
  constructor(
    private router: Router,
    private route: ActivatedRoute
  ) { }

  ngOnInit() {
    //inita
    this.serverId = this.route.snapshot.params["id"];
    this.connected = this.route.snapshot.params["connected"];

    this.route.params.subscribe(params => {
      this.serverId = params["id"];
      this.connected = params["connected"];        
    })
  }

  routeBack() {
    this.router.navigate(['/servers']);
  }

}



