import { LogService } from "./log.service";
import { Injectable } from "@angular/core";

// import { ServerInfo } from "../server/server.component";
@Injectable()
export class ServersService {
    servers = [
        {
            id: 10,
            connected: true,
        },
        {
            id: 20,
            connected: false,
        },
        {
            id: 40,
            connected: false,
        },
        {
            id: 42,
            connected: true,
        },
    ];

    constructor(
        private logService:LogService
    ) {}
    
    addServer(id:number, connected: boolean) {
        if (this.servers.some(el => el.id === id))
            return alert('id already exists')
        this.servers.push({id,connected});

        this.logService.log(`server ${id} has been created => ${connected ? 'Connected' : 'Disconnected'}`)
    }

    setServer(id: number, connected: boolean) {
        console.log(this.servers.find(el => el.id === id));
        this.servers.find(el => el.id === id).connected = connected;
    }

    removeServer(i) {
        this.servers.splice(i, 1);
    }
}



