import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { ServerInfo } from '../server/server.component';
import { LogService } from '../services/log.service';
import { Router, ActivatedRoute } from '@angular/router';
import { ServersService } from '../services/servers.service';

@Component({
  selector: 'app-servers',
  templateUrl: './servers.component.html',
  styleUrls: ['./servers.component.css']
})
export class ServersComponent implements OnInit {
  @ViewChild('btn') btn: ElementRef;
  serverId;
  logs = ['Application started....', '4 Servers initalized'];
  servers = [
    {
      id: 10,
      connected: true,
    },
    {
      id: 20,
      connected: false,
    },
    {
      id: 40,
      connected: false,
    },
    {
      id: 42,
      connected: true,
    },
  ];

  constructor(
    private logService:LogService, 
    private router: Router,
    private route: ActivatedRoute,
    private serversService: ServersService
  ) { }

  ngOnInit() {
    this.logs = this.logService.logs;
    this.servers = this.serversService.servers;

    setTimeout(() => {
      this.btn.nativeElement.classList.toggle('btn-primary');
      this.btn.nativeElement.classList.toggle('btn-danger');
    }, 2000)
    // console.log(this.btn);
  }

  addConnectedServer() {
    // if (this.servers.some(el => el.id === this.serverId))
    //   return alert('id already exists')
    // this.servers.push({
    //   id: this.serverId,
    //   connected: true
    // });
    this.serversService.addServer(this.serverId, true);
    this.serverId = '';
  }

  addServer() {
    // if (this.servers.some(el => el.id === this.serverId))
    //   return alert('id already exists')
    // this.servers.push({
    //   id: this.serverId,
    //   connected: false
    // });

    this.serversService.addServer(this.serverId, false);
    this.serverId = '';
  }

  removeServer(i) {
    this.serversService.removeServer(i);
    // this.servers.splice(i, 1);
  }


  isEmpty() {
    return this.serverId && this.serverId !== '';
  }

  updateServerInfo(event: ServerInfo) {
    this.serversService.setServer(event.serverId, event.connected);
    this.logService.log(`Server ${event.serverId} => ${event.connected ? 'Connected' : 'Disconnected'}`);
    //this.logs.push(`Server ${event.serverId} => ${event.connected ? 'Connected' : 'Disconnected'}`)
  }


  navigateHome() {
    //absolute route
    this.router.navigate(['/']);
  }

  navigateToError() {
    //relative route
    this.router.navigate(['/test'], {relativeTo: this.route});
    
  }
}



